.container-fluid {
    margin-top: 40px;
    max-width: 580px;
    padding-left: 5px;
    padding-right: 5px;
    margin: 0 auto;
}

.info {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.title {
    font-family: 'Roboto Slab', sans-serif;
    font-size: 36px;
    margin-top: 60px;
    margin-bottom: 30px;
    text-align: center;
    width: 100%;
    color: #222;
    font-weight: 400;
}

h2 {
    font-family: 'Roboto Slab', sans-serif;
    margin-bottom: 0;
}

.subtitle {
    font-size: 16px;
    margin-bottom: 8px;
    display: flex;
    justify-content: space-between;
}

.rules {
    background-color: #f1f1f1;
    padding: 20px;
    padding-left: 40px;
    border-radius: 10px;
    user-select: none;
}

.rules li {
    padding-bottom: 4px;
    font-size: 17px;
}

.btn {
    padding: 10px 28px;
    flex: 1;
    font-size: 17px;
    border: 1px solid #555;
    border-radius: 20px;
    user-select: none;
    cursor: pointer;
    text-align: center;
    max-width: 40px;
}

.flex-row {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    line-height: 30px;
}

.piece:before {
    content: '';
    float: left;
    padding-top: 100%;
}

.piece {
    position: relative;
    background-color: #fff;
    border: 1px dotted #666;
    cursor: pointer;
    font-weight: bold;
    text-align: center;
    flex: 1 0 auto;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.piece.locked {
    cursor: initial;
}

.piece.invalid {
    background-color: rgba(217, 30, 24, 0.15);
    animation: fadeInBackground ease-in 0.1s;
}

@keyframes fadeInBackground {
    0% {
        background-color: #fff;
    }
    100% {
        background-color: rgba(217, 30, 24, 0.15);
    }
}

.congrats {
    animation: fadeIn ease-out 0.8s;
    opacity: 1;
}
@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

a {
    color: #222;
}
